:root {
  --white: hsl(0, 0%, 100%);
  --darkWhite: hsl(220, 0%, 98%);
  --gray: hsl(231, 8%, 61%);
  --lightGray: hsl(223, 18%, 96%);
  --darkGray: hsl(220, 16%, 96%);
  --blue: hsl(231, 26%, 24%);
  --darkBlue: hsl(233, 11%, 43%);
  --lightBlue: hsl(192, 69%, 51%);
  --green: hsl(136, 64%, 51%);
  --lightGreen: hsl(158, 61%, 49%);
}

:root {
  --font-public: "Public Sans", sans-serif;
}

body {
  background-color: var(--darkWhite);
}

.container {
  max-width: 32.7rem;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 36em) {
  .container {
    max-width: 54rem;
  }
}
@media (min-width: 48em) {
  .container {
    max-width: 70rem;
  }
}
@media (min-width: 62em) {
  .container {
    max-width: 90rem;
  }
}
@media (min-width: 75em) {
  .container {
    max-width: 110rem;
  }
}

.hover-blue:hover .nav__link {
  color: var(--blue);
}

.hover-green {
  cursor: pointer;
  transition: all 0.2s;
}
.hover-green path {
  transition: fill 0.4s;
}
.hover-green:hover {
  color: var(--lightGreen);
}
.hover-green:hover path {
  fill: var(--lightGreen);
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
ul {
  margin: 0;
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

li {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
  font-family: var(--font-public);
}

.heading-primary {
  font-weight: 300;
  color: var(--blue);
  font-size: 4rem;
  line-height: 1.2;
}
@media (min-width: 62em) {
  .heading-primary {
    font-size: 4.8rem;
  }
}
@media (min-width: 75em) {
  .heading-primary {
    font-size: 5.6rem;
  }
}

.heading-secondary {
  font-weight: 300;
  color: var(--blue);
  font-size: 3.2rem;
  text-align: center;
}
@media (min-width: 62em) {
  .heading-secondary {
    font-size: 3.6rem;
    text-align: left;
  }
}

.heading-tertiary {
  font-weight: 300;
  color: var(--blue);
  font-size: 2rem;
}
@media (min-width: 62em) {
  .heading-tertiary {
    font-size: 2.4rem;
  }
}

.heading-fourth {
  font-weight: 300;
  color: var(--blue);
  font-size: 1.6rem;
}

.description {
  color: var(--gray);
  font-size: 1.5rem;
}
@media (min-width: 62em) {
  .description {
    font-size: 1.6rem;
  }
}

.button {
  background-image: linear-gradient(to right bottom, var(--green), var(--lightBlue));
  color: var(--white);
  border-radius: 22px;
  padding: 1rem 3.4rem;
  border: none;
  font-weight: 700;
  cursor: pointer;
  transition: opacity 0.4s;
}
.button:hover {
  opacity: 0.8;
}

.overlay {
  position: absolute;
  top: 6.4rem;
  z-index: 10;
  width: 100%;
  height: calc(100vh - 6.4rem);
  background-image: linear-gradient(to bottom, var(--blue), transparent);
  display: none;
}
.overlay.show {
  display: block;
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 100;
  background-color: var(--white);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1);
}

.nav {
  display: flex;
  height: 6.4rem;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
@media (min-width: 48em) {
  .nav {
    height: 8rem;
  }
}
.nav__menu {
  width: 100%;
  position: absolute;
  top: -30rem;
  padding: 3.2rem 0;
  background-color: var(--white);
  border-radius: 4px;
  transition: all 0.4s;
}
.nav__menu.show {
  transition: all 0.4s;
  top: 8.8rem;
}
@media (min-width: 48em) {
  .nav__menu {
    position: static;
    top: 0;
    padding: 0;
    width: auto;
    display: flex;
    align-items: center;
    height: 8rem;
  }
}
.nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 2.4rem;
}
@media (min-width: 48em) {
  .nav__list {
    flex-direction: row;
    column-gap: 2rem;
    font-size: 1.4rem;
    height: 100%;
  }
}
@media (min-width: 62em) {
  .nav__list {
    column-gap: 3rem;
  }
}
@media (min-width: 48em) {
  .nav__item {
    height: 100%;
    padding: 3.2rem 0 2.8rem;
    position: relative;
  }
  .nav__item::after {
    position: absolute;
    bottom: 0;
    transition: all 0.4s;
    content: "";
    display: block;
    width: 0;
    height: 4px;
    background-image: linear-gradient(to right bottom, var(--green), var(--lightBlue));
  }
  .nav__item:hover::after {
    width: 100%;
  }
}
.nav__link {
  color: var(--blue);
  text-decoration: none;
  display: block;
}
@media (min-width: 48em) {
  .nav__link {
    transition: all 0.4s;
    color: var(--gray);
  }
}
.nav__toggle {
  cursor: pointer;
}
@media (min-width: 48em) {
  .nav__toggle {
    display: none;
  }
}
.nav__close {
  display: none;
}
.nav__button {
  display: none;
}
@media (min-width: 48em) {
  .nav__button {
    display: block;
  }
}

.footer {
  padding: 4rem 0;
  background-color: var(--blue);
}
.footer__container {
  display: grid;
  row-gap: 3.2rem;
}
@media (min-width: 62em) {
  .footer__container {
    grid-template-columns: repeat(2, max-content) auto;
    grid-template-rows: repeat(3, 2.6rem);
    row-gap: 0;
    column-gap: 13rem;
  }
}
.footer__logo {
  display: block;
}
.footer__logo-img {
  margin: 0 auto;
}
@media (min-width: 62em) {
  .footer__logo-img {
    margin: 0;
  }
}
.footer__share {
  display: flex;
  justify-content: center;
  column-gap: 1.6rem;
}
@media (min-width: 62em) {
  .footer__share {
    grid-row: 3/4;
    align-self: end;
    justify-content: flex-start;
  }
}
.footer__nav-list {
  display: grid;
  justify-items: center;
  row-gap: 0.8rem;
  font-size: 1.5rem;
}
@media (min-width: 62em) {
  .footer__nav-list {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 10rem;
    grid-row: 1/4;
    justify-items: start;
  }
}
.footer__nav-link {
  color: var(--white);
  text-decoration: none;
}
.footer__button {
  justify-self: center;
}
.footer__copyright {
  text-align: center;
  color: var(--white);
  margin-top: 0.8rem;
}
@media (min-width: 62em) {
  .footer__copyright {
    grid-row: 1/4;
    justify-self: end;
    margin-top: 0;
    text-align: right;
  }
}

.copyright {
  display: block;
  margin-top: 2.4rem;
  font-size: 1.5rem;
}
@media (min-width: 62em) {
  .copyright {
    margin-top: 1.4rem;
  }
}

.hero {
  display: flex;
  flex-direction: column-reverse;
  padding-bottom: 8.8rem;
  row-gap: 4rem;
}
@media (min-width: 62em) {
  .hero {
    margin-top: 8rem;
    padding-bottom: 0;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.hero__img-content {
  background-image: url("../images/bg-intro-mobile.svg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  height: 34.5rem;
  margin: 0 calc(50% - 50vw);
  width: 100vw;
}
@media (min-width: 36em) {
  .hero__img-content {
    height: 56rem;
    background-position: center center;
  }
}
@media (min-width: 48em) {
  .hero__img-content {
    height: 65rem;
  }
}
@media (min-width: 62em) {
  .hero__img-content {
    overflow-x: clip;
    background-image: url("../images/bg-intro-desktop.svg");
    width: 69%;
    height: 60rem;
    background-position: -2rem 50%;
    background-size: 200%;
  }
}
@media (min-width: 75em) {
  .hero__img-content {
    height: 76.5rem;
    background-position: -6rem 50%;
    background-size: 200%;
  }
}
.hero__img {
  position: absolute;
  top: -5rem;
  left: 50%;
  transform: translateX(-50%);
  object-fit: cover;
  height: 120%;
}
@media (min-width: 36em) {
  .hero__img {
    top: -13rem;
  }
}
@media (min-width: 62em) {
  .hero__img {
    transform: none;
    left: auto;
    right: -10rem;
    height: 130%;
    top: -10rem;
    width: 100%;
  }
}
@media (min-width: 75em) {
  .hero__img {
    height: 118%;
    top: -8rem;
    right: -8rem;
  }
}
.hero__text-content {
  text-align: center;
}
@media (min-width: 62em) {
  .hero__text-content {
    width: 42%;
    text-align: left;
  }
}
.hero__title {
  margin-bottom: 1.6rem;
}
.hero__description {
  margin-bottom: 3.2rem;
  font-size: 1.5rem;
}

.feature {
  padding: 6.4rem 0;
  background-color: var(--darkGray);
}
.feature__title-content {
  margin-bottom: 5.6rem;
}
@media (min-width: 62em) {
  .feature__title-content {
    width: 60rem;
  }
}
.feature__heading {
  margin-bottom: 1.4rem;
}
.feature__container {
  display: grid;
  row-gap: 3.2rem;
}
@media (min-width: 48em) {
  .feature__container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 62em) {
  .feature__container {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 3rem;
  }
}
.feature__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  row-gap: 1.6rem;
}
@media (min-width: 62em) {
  .feature__content {
    align-items: flex-start;
    text-align: left;
  }
}
.feature__img {
  margin-bottom: 0.8rem;
}

.article {
  padding: 8.8rem 0;
}
.article__heading {
  margin-bottom: 3rem;
}
.article__container {
  display: grid;
  row-gap: 2.4rem;
}
@media (min-width: 48em) {
  .article__container {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2.4rem;
  }
}
@media (min-width: 62em) {
  .article__container {
    grid-template-columns: repeat(4, 1fr);
  }
}
.article__content {
  display: grid;
  grid-template-rows: 20rem auto;
  border-radius: 5px;
}
@media (min-width: 36em) {
  .article__content {
    grid-template-rows: 24rem auto;
  }
}
@media (min-width: 62em) {
  .article__content {
    grid-template-rows: 18rem auto;
  }
}
@media (min-width: 75em) {
  .article__content {
    grid-template-rows: 20rem auto;
  }
}
.article__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.article__inner {
  padding: 2.4rem 3rem;
  display: flex;
  flex-direction: column;
  row-gap: 0.8rem;
  background-color: var(--white);
}
@media (min-width: 62em) {
  .article__inner {
    padding: 2.4rem 1.8rem;
  }
}
@media (min-width: 75em) {
  .article__inner {
    padding: 2.4rem;
  }
}
.article__description {
  font-size: 1.3rem;
}
.article__author {
  font-size: 1rem;
  color: var(--gray);
}